const TypewriterEffect = ({
  texts,
  typingSpeed = 150,
  eraseSpeed = 100,
  pauseTime = 2000,
}) => {
  const [currentText, setCurrentText] = useState("");
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isTyping, setIsTyping] = useState(true);

  useEffect(() => {
    let timer;
    if (isTyping) {
      if (currentText.length < texts[currentIndex].length) {
        timer = setTimeout(() => {
          setCurrentText(texts[currentIndex].slice(0, currentText.length + 1));
        }, typingSpeed);
      } else {
        setIsTyping(false);
        timer = setTimeout(() => {
          setIsTyping(true);
        }, pauseTime);
      }
    } else {
      if (currentText.length > 0) {
        timer = setTimeout(() => {
          setCurrentText(texts[currentIndex].slice(0, currentText.length - 1));
        }, eraseSpeed);
      } else {
        setCurrentIndex((prevIndex) => (prevIndex + 1) % texts.length); // move to next text
        setIsTyping(true); // Start typing again
      }
    }
    return () => clearTimeout(timer);
  }, [
    currentText,
    currentIndex,
    isTyping,
    texts,
    typingSpeed,
    eraseSpeed,
    pauseTime,
  ]);
  return <span className="text-nord8">{currentText}</span>;
};

